<template>
	<div class="customers">
		<!--顶部菜单-->
		<nav-bar class="customers-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				{{title}}
			</div>
			<div slot="right">
				<span @click="addcustomerClick">新增</span>
			</div>
		</nav-bar>
		<top-banner-navs @customerClick="customerClick" :banner-navs-arr="topBannerNav" ref="topnavs"/>
		<!--滚动-->
		<scroll class="customers-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
				:data="customerLists"
		>
		<customers-content :customers-arr="customerLists"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import Scroll from 'components/common/scroll/Scroll'
import TopBannerNavs from 'components/common/topbannernavs/TopBannerNavs'


import CustomersContent from 'components/contents/candscontents/CustomersContent'
//混入
import {itemCustomerMixin} from 'common/mixin'

export default {
  name: 'Customers',
  components:{
	NavBar,
	TopBannerNavs,
	Scroll,
	CustomersContent
  },
  mixins: [itemCustomerMixin],			//混入
  data(){
	return{
		currentType:"customers",
		currentIndex:0,
		topBannerNav:[
			'客户','供应商',
		],
		titleObj:{
				customers:'客户管理',
				supplier:'供应商管理',
		}
	}
  },
  methods:{
	//客户和供应商的点击
	addcustomerClick(){
		this.$router.push('/addcustomer')
	},
	customerClick(index){
		switch(index){
			case 0:
				this.currentType="customers"
				break
			case 1:
				this.currentType="supplier"
				break
		}
		this.$router.replace('/customers/'+this.currentType).catch(res=>{res})		
	},
	//返回按钮
	backClick(){
		this.$router.replace("/home").catch(res=>{res})
	}
  },
  computed:{
	//标题显示
	title(){
		return this.titleObj[this.currentType]
	}
  },
  mounted(){
  },
  activated(){
	//判断是客户还是供应商
	this.currentType=this.$route.params.id
	if(this.currentType=='supplier'){
		this.$refs.topnavs.currentIndex=1
	}else{
		this.$refs.topnavs.currentIndex=0
	}
  }
}
</script>


<style spoced>
.customers{
    height: 100vh;
    position: relative;
	z-index:11;
	background:#eaeaea;
}
.customers-scroller{
	height:calc(100% - 89px);
    right: 0;
    left: 0;
	overflow:hidden;
    position: relative;
  }
  .icon-left{
	text-align:left;
	position:relative;
  }
  .icon-left img{
	position:absolute;
	top:10px;
	left:10px;
  }


</style>
